/**
 * ì¹´í…Œê³ ë¦¬ ë¬¸ìì—´ì˜ ì²« ê¸€ìë¥¼ ëŒ€ë¬¸ìë¡œ ë³€í™˜í•˜ì—¬ í‘œì‹œëª… ìƒì„±
 * @param category - ì¹´í…Œê³ ë¦¬ ë¬¸ìì—´
 * @returns í‘œì‹œìš© ì¹´í…Œê³ ë¦¬ ì´ë¦„
 */
export const formatCategoryDisplayName = (category: string): string => {
  if (!category) return 'ê¸°íƒ€'
  
  // íŠ¹ë³„í•œ ê²½ìš° ì²˜ë¦¬
  const specialCases: { [key: string]: string } = {
    'plc': 'PLC',
    'iot': 'IoT',
    'ë°ì´í„°ë¶„ì„': 'ë°ì´í„° ë¶„ì„',
    'ë¡œë´‡ê³µí•™': 'ë¡œë´‡ ê³µí•™',
    'ê¸°íƒ€': 'ê¸°íƒ€',
    'all': 'ì „ì²´'
  }
  
  const lowerCategory = category.toLowerCase()
  if (specialCases[lowerCategory]) {
    return specialCases[lowerCategory]
  }
  
  // ì¼ë°˜ì ì¸ ê²½ìš°: ì²« ê¸€ìë§Œ ëŒ€ë¬¸ìë¡œ ë³€í™˜
  return category.charAt(0).toUpperCase() + category.slice(1)
}

/**
 * ì±… ëª©ë¡ì—ì„œ ëª¨ë“  ê³ ìœ  ì¹´í…Œê³ ë¦¬ë¥¼ ì¶”ì¶œ
 * @param books - ì±… ëª©ë¡
 * @returns ê³ ìœ  ì¹´í…Œê³ ë¦¬ ë°°ì—´ (ì†Œë¬¸ìë¡œ ì •ê·œí™”)
 */
export const extractUniqueCategories = (books: any[]): string[] => {
  const categories = new Set<string>()
  
  // ê¸°ë³¸ ì¹´í…Œê³ ë¦¬ ì¶”ê°€
  categories.add('all')
  
  // ì±…ì—ì„œ ì¹´í…Œê³ ë¦¬ ì¶”ì¶œ
  books.forEach(book => {
    if (book.category) {
      categories.add(book.category.toLowerCase())
    }
  })
  
  return Array.from(categories).sort()
}

/**
 * ì¹´í…Œê³ ë¦¬ë³„ í‘œì‹œëª… ë§¤í•‘ ê°ì²´ ìƒì„±
 * @param categories - ì¹´í…Œê³ ë¦¬ ë°°ì—´
 * @returns ì¹´í…Œê³ ë¦¬ í‘œì‹œëª… ë§¤í•‘ ê°ì²´
 */
export const createCategoryDisplayNames = (categories: string[]): { [key: string]: string } => {
  const displayNames: { [key: string]: string } = {}
  
  categories.forEach(category => {
    displayNames[category] = formatCategoryDisplayName(category)
  })
  
  return displayNames
}
