import React, { createContext, useContext, useState, useEffect, useCallback, useMemo } from 'react';
import { authAPI } from '../services/api';

interface User {
  username: string;
  isAdmin: boolean;
}

interface AuthContextType {
  user: User | null;
  login: (username: string, password: string) => Promise<void>;
  logout: () => void;
  isAuthenticated: boolean;
  isLoading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  // token ìƒíƒœ ì œê±°: HttpOnly ì¿ í‚¤ ì‚¬ìš© ê°€ì •
  const [isLoading, setIsLoading] = useState(false); // ì´ˆê¸° ë¡œë”© ìƒíƒœë¥¼ falseë¡œ ì„¤ì •

  // useEffectì—ì„œ ì‚¬ìš©ì ì •ë³´ë¥¼ ë¡œë“œ (í† í°ì´ ì¿ í‚¤ì— ìˆì„ ê²½ìš°)
  useEffect(() => {
    const loadUser = async () => {
      try {
        // verify APIê°€ HttpOnly ì¿ í‚¤ë¥¼ ì‚¬ìš©í•˜ì—¬ ì¸ì¦í•˜ê³  ì‚¬ìš©ì ì •ë³´ë¥¼ ë°˜í™˜í•œë‹¤ê³  ê°€ì •
        const response = await authAPI.verify();
        setUser(response.data.user);
      } catch (error) {
        // í† í°ì´ ì—†ê±°ë‚˜ ìœ íš¨í•˜ì§€ ì•Šìœ¼ë©´ userë¥¼ nullë¡œ ì„¤ì •
        setUser(null);
      }
    };
    
    // ì•± ë¡œë“œ ì‹œ verify APIë¥¼ í˜¸ì¶œí•˜ì—¬ ê¸°ì¡´ ì„¸ì…˜ í™•ì¸
    loadUser();
  }, []); // ì•± ë¡œë“œ ì‹œ í•œ ë²ˆë§Œ ì‹¤í–‰

  const login = useCallback(async (username: string, password: string) => {
    try {
      setIsLoading(true);
      // ë¡œê·¸ì¸ API í˜¸ì¶œ: ì„œë²„ëŠ” ì„±ê³µ ì‹œ HttpOnly ì¿ í‚¤ì— í† í°ì„ ì„¤ì •í•˜ê³  ì‚¬ìš©ì ì •ë³´ë¥¼ ë°˜í™˜
      const response = await authAPI.login(username, password);
      setUser(response.data.user);
      setIsLoading(false);
    } catch (error: any) {
      setIsLoading(false);
      // setUser(null); // ë¡œê·¸ì¸ ì‹¤íŒ¨ ì‹œ user ì´ˆê¸°í™”
      throw new Error(error.response?.data?.message || 'ë¡œê·¸ì¸ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.');
    }
  }, []);

  const logout = useCallback(async () => {
    // ë¡œê·¸ì•„ì›ƒ API í˜¸ì¶œ: ì„œë²„ëŠ” ì¿ í‚¤ë¥¼ ì œê±°
    try {
      await authAPI.logout(); 
    } catch (error) {
      // ì„œë²„ ì¸¡ ë¡œê·¸ì•„ì›ƒ ì‹¤íŒ¨ ì‹œì—ë„ í´ë¼ì´ì–¸íŠ¸ ìƒíƒœëŠ” ì´ˆê¸°í™”
      console.error('ì„œë²„ ë¡œê·¸ì•„ì›ƒ ì˜¤ë¥˜:', error);
    }
    setUser(null);
  }, []);

  // Context valueë¥¼ useMemoë¡œ ë©”ëª¨ì´ì œì´ì…˜í•˜ì—¬ ë¶ˆí•„ìš”í•œ ë¦¬ë Œë”ë§ ë°©ì§€
  const value = useMemo(() => ({
    user,
    login,
    logout,
    // isAuthenticatedëŠ” user ê°ì²´ì˜ ìœ ë¬´ë¡œ íŒë‹¨
    isAuthenticated: !!user,
    isLoading,
  }), [user, login, logout, isLoading]);

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};


