/**
 * ë‚ ì§œ ë¬¸ìì—´ì„ í•œêµ­ì–´ í˜•ì‹ìœ¼ë¡œ í¬ë§·íŒ…
 * @param dateString - í¬ë§·íŒ…í•  ë‚ ì§œ ë¬¸ìì—´
 * @returns í•œêµ­ì–´ í˜•ì‹ì˜ ë‚ ì§œ ë¬¸ìì—´ (ì˜ˆ: "2024. 1. 15.")
 */
export const formatDate = (dateString: string): string => {
  try {
    const date = new Date(dateString)
    return date.toLocaleDateString('ko-KR')
  } catch (error) {
    console.error('ë‚ ì§œ í¬ë§·íŒ… ì˜¤ë¥˜:', error)
    return 'ë‚ ì§œ ì •ë³´ ì—†ìŒ'
  }
}

/**
 * ë‚ ì§œ ë¬¸ìì—´ì„ ìƒëŒ€ì  ì‹œê°„ìœ¼ë¡œ í¬ë§·íŒ… (ì˜ˆ: "3ë¶„ ì „", "2ì‹œê°„ ì „", "3ì¼ ì „")
 * @param dateString - í¬ë§·íŒ…í•  ë‚ ì§œ ë¬¸ìì—´
 * @returns ìƒëŒ€ì  ì‹œê°„ ë¬¸ìì—´
 */
export const formatRelativeDate = (dateString: string): string => {
  try {
    const date = new Date(dateString)
    const now = new Date()
    const diffInMs = now.getTime() - date.getTime()
    
    // ìŒìˆ˜ì¸ ê²½ìš° ë¯¸ë˜ ë‚ ì§œ (ë°©ê¸ˆ ì „ìœ¼ë¡œ ì²˜ë¦¬)
    if (diffInMs < 0) return 'ë°©ê¸ˆ ì „'
    
    // ë¶„ ë‹¨ìœ„ ê³„ì‚°
    const diffInMinutes = Math.floor(diffInMs / (1000 * 60))
    if (diffInMinutes < 1) return 'ë°©ê¸ˆ ì „'
    if (diffInMinutes < 60) return `${diffInMinutes}ë¶„ ì „`
    
    // ì‹œê°„ ë‹¨ìœ„ ê³„ì‚°
    const diffInHours = Math.floor(diffInMs / (1000 * 60 * 60))
    if (diffInHours < 24) return `${diffInHours}ì‹œê°„ ì „`
    
    // ì¼ ë‹¨ìœ„ ê³„ì‚°
    const diffInDays = Math.floor(diffInMs / (1000 * 60 * 60 * 24))
    if (diffInDays === 1) return 'ì–´ì œ'
    if (diffInDays < 7) return `${diffInDays}ì¼ ì „`
    
    // ì£¼ ë‹¨ìœ„ ê³„ì‚°
    const diffInWeeks = Math.floor(diffInDays / 7)
    if (diffInWeeks < 4) return `${diffInWeeks}ì£¼ ì „`
    
    // ì›” ë‹¨ìœ„ ê³„ì‚° (30ì¼ ê¸°ì¤€)
    const diffInMonths = Math.floor(diffInDays / 30)
    if (diffInMonths < 12) return `${diffInMonths}ê°œì›” ì „`
    
    // ë…„ ë‹¨ìœ„ ê³„ì‚°
    const diffInYears = Math.floor(diffInDays / 365)
    return `${diffInYears}ë…„ ì „`
  } catch (error) {
    console.error('ìƒëŒ€ì  ë‚ ì§œ í¬ë§·íŒ… ì˜¤ë¥˜:', error)
    return 'ë‚ ì§œ ì •ë³´ ì—†ìŒ'
  }
}

/**
 * ëª¨ë˜í•œ Intl.RelativeTimeFormat APIë¥¼ ì‚¬ìš©í•œ ìƒëŒ€ì  ì‹œê°„ í¬ë§·íŒ…
 * @param dateString - í¬ë§·íŒ…í•  ë‚ ì§œ ë¬¸ìì—´
 * @param locale - ë¡œì¼€ì¼ (ê¸°ë³¸ê°’: 'ko-KR')
 * @returns ìƒëŒ€ì  ì‹œê°„ ë¬¸ìì—´
 */
export const formatRelativeDateModern = (dateString: string, locale: string = 'ko-KR'): string => {
  try {
    const date = new Date(dateString)
    const now = new Date()
    const diffInSeconds = (now.getTime() - date.getTime()) / 1000
    
    // ìŒìˆ˜ì¸ ê²½ìš° ë¯¸ë˜ ë‚ ì§œ
    if (diffInSeconds < 0) return 'ë°©ê¸ˆ ì „'
    
    const formatter = new Intl.RelativeTimeFormat(locale, { numeric: 'auto' })
    
    // ì´ˆ ë‹¨ìœ„
    if (diffInSeconds < 60) {
      return 'ë°©ê¸ˆ ì „'
    }
    
    // ë¶„ ë‹¨ìœ„
    if (diffInSeconds < 3600) {
      return formatter.format(Math.floor(-diffInSeconds / 60), 'minute')
    }
    
    // ì‹œê°„ ë‹¨ìœ„
    if (diffInSeconds < 86400) {
      return formatter.format(Math.floor(-diffInSeconds / 3600), 'hour')
    }
    
    // ì¼ ë‹¨ìœ„
    if (diffInSeconds < 2592000) { // 30ì¼ ë¯¸ë§Œ
      return formatter.format(Math.floor(-diffInSeconds / 86400), 'day')
    }
    
    // ì›” ë‹¨ìœ„
    if (diffInSeconds < 31536000) { // 365ì¼ ë¯¸ë§Œ
      return formatter.format(Math.floor(-diffInSeconds / 2592000), 'month')
    }
    
    // ë…„ ë‹¨ìœ„
    return formatter.format(Math.floor(-diffInSeconds / 31536000), 'year')
  } catch (error) {
    console.error('ëª¨ë˜ ìƒëŒ€ì  ë‚ ì§œ í¬ë§·íŒ… ì˜¤ë¥˜:', error)
    return 'ë‚ ì§œ ì •ë³´ ì—†ìŒ'
  }
}

/**
 * ë‚ ì§œê°€ ìœ íš¨í•œì§€ í™•ì¸
 * @param dateString - í™•ì¸í•  ë‚ ì§œ ë¬¸ìì—´
 * @returns ìœ íš¨í•œ ë‚ ì§œì¸ì§€ ì—¬ë¶€
 */
export const isValidDate = (dateString: string): boolean => {
  try {
    const date = new Date(dateString)
    return !isNaN(date.getTime())
  } catch {
    return false
  }
}

/**
 * ë‘ ë‚ ì§œ ì‚¬ì´ì˜ ì°¨ì´ë¥¼ ê³„ì‚°
 * @param startDate - ì‹œì‘ ë‚ ì§œ ë¬¸ìì—´
 * @param endDate - ì¢…ë£Œ ë‚ ì§œ ë¬¸ìì—´ (ê¸°ë³¸ê°’: í˜„ì¬ ì‹œê°„)
 * @returns ì°¨ì´ ì •ë³´ ê°ì²´
 */
export const getDateDifference = (
  startDate: string, 
  endDate: string = new Date().toISOString()
): {
  days: number
  hours: number
  minutes: number
  totalMs: number
} => {
  try {
    const start = new Date(startDate)
    const end = new Date(endDate)
    const diffInMs = Math.abs(end.getTime() - start.getTime())
    
    const days = Math.floor(diffInMs / (1000 * 60 * 60 * 24))
    const hours = Math.floor((diffInMs % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
    const minutes = Math.floor((diffInMs % (1000 * 60 * 60)) / (1000 * 60))
    
    return {
      days,
      hours,
      minutes,
      totalMs: diffInMs
    }
  } catch (error) {
    console.error('ë‚ ì§œ ì°¨ì´ ê³„ì‚° ì˜¤ë¥˜:', error)
    return {
      days: 0,
      hours: 0,
      minutes: 0,
      totalMs: 0
    }
  }
}

/**
 * ë‚ ì§œë¥¼ ë‹¤ì–‘í•œ í˜•ì‹ìœ¼ë¡œ í¬ë§·íŒ…
 * @param dateString - í¬ë§·íŒ…í•  ë‚ ì§œ ë¬¸ìì—´
 * @param format - í¬ë§· íƒ€ì… ('short', 'long', 'time', 'datetime')
 * @returns í¬ë§·íŒ…ëœ ë‚ ì§œ ë¬¸ìì—´
 */
export const formatDateByType = (
  dateString: string, 
  format: 'short' | 'long' | 'time' | 'datetime' = 'short'
): string => {
  try {
    const date = new Date(dateString)
    
    switch (format) {
      case 'short':
        return date.toLocaleDateString('ko-KR', {
          year: 'numeric',
          month: 'numeric',
          day: 'numeric'
        })
      case 'long':
        return date.toLocaleDateString('ko-KR', {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
          weekday: 'long'
        })
      case 'time':
        return date.toLocaleTimeString('ko-KR', {
          hour: '2-digit',
          minute: '2-digit'
        })
      case 'datetime':
        return date.toLocaleString('ko-KR', {
          year: 'numeric',
          month: 'numeric',
          day: 'numeric',
          hour: '2-digit',
          minute: '2-digit'
        })
      default:
        return date.toLocaleDateString('ko-KR')
    }
  } catch (error) {
    console.error('ë‚ ì§œ íƒ€ì…ë³„ í¬ë§·íŒ… ì˜¤ë¥˜:', error)
    return 'ë‚ ì§œ ì •ë³´ ì—†ìŒ'
  }
}

/**
 * ë‚ ì§œê°€ ì˜¤ëŠ˜ì¸ì§€ í™•ì¸
 * @param dateString - í™•ì¸í•  ë‚ ì§œ ë¬¸ìì—´
 * @returns ì˜¤ëŠ˜ì¸ì§€ ì—¬ë¶€
 */
export const isToday = (dateString: string): boolean => {
  try {
    const date = new Date(dateString)
    const today = new Date()
    
    return date.getDate() === today.getDate() &&
           date.getMonth() === today.getMonth() &&
           date.getFullYear() === today.getFullYear()
  } catch {
    return false
  }
}

/**
 * ë‚ ì§œê°€ ìµœê·¼ Nì¼ ì´ë‚´ì¸ì§€ í™•ì¸
 * @param dateString - í™•ì¸í•  ë‚ ì§œ ë¬¸ìì—´
 * @param days - ê¸°ì¤€ ì¼ìˆ˜ (ê¸°ë³¸ê°’: 7)
 * @returns ìµœê·¼ Nì¼ ì´ë‚´ì¸ì§€ ì—¬ë¶€
 */
export const isRecent = (dateString: string, days: number = 7): boolean => {
  try {
    const date = new Date(dateString)
    const now = new Date()
    const diffInMs = now.getTime() - date.getTime()
    const diffInDays = Math.floor(diffInMs / (1000 * 60 * 60 * 24))
    
    return diffInDays <= days && diffInDays >= 0
  } catch {
    return false
  }
}
